.header {
    background-color: #fff;
    .toggle-sidebar {
        cursor: pointer;
    }
    .notifications-container{
        display: inline-block;
        margin-left: 20px;
        .toggle-notifications {
            background-color: #f9f9f9;
            padding: 10px;
            border-radius: 50%;
            color: #555;
            cursor: pointer;
        }
        .dropdown-menu {
            min-width: 600px;
            padding: 20px;
            border: none;
            padding: 0;
            top: 10px !important;

            &:before{
                content:'';
                position: absolute;
                top: -20px;
                left: 11px ;
                border-style: solid;
                border-width: 10px;
                border-color: transparent transparent  #333 transparent;
                
            }
            .noti-head {
                background-color: #333;
                padding: 15px;
                color:#FFF ;
                span{
                    float: right;
                }
            }
            .noti-content{
                padding: 20px;
                ul {
                    margin: 0;
                    li {
                        padding: 10px;
                        i{
                            background-color: #EEE;
                            padding: 10px;
                            border-radius: 50%;
                            width: 45px;
                            height: 45px;
                            line-height: 26px;
                            text-align: center;
                            margin-right: 10px;
                            position: relative;
                            top: -2px;
                        }
                    }
                }
            }
            .noti-foot{
                display: block;
                padding: 10px;
                border-top:1px solid #EEEE;
                &:hover{
                    text-decoration: none;
                }
            }
        }
    }
    
}